* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}


.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.country-flag {
  max-width: 70px;
  max-height: 60px;
}

.country-name {
  font-size: large;
  margin-left: 15px;
}

.country-item {
  display: flex;
  margin-bottom: 15px;
}

.country-title {
  margin: 0;
}